React = require("react");

var ImageForm = React.createClass({displayName: 'ImageForm',
  propTypes: {
    onAddUrl: React.PropTypes.func.isRequired
  },

  getInitialState: function() {
    return { url: '' };
  },

  render: function() {
    return (
      React.createElement("form", {onSubmit: this.onSubmit}, 
        React.createElement("input", {type: "text", placeholder: "Enter URL", size: "30", 
               value: this.state.url, onChange: this.handleChange}), 
        React.createElement("input", {type: "submit", value: "Add URL"})
      )
    );
  },

  handleChange: function(e) {
    this.setState({url: e.target.value});
  },

  onSubmit: function(e) {
    e.preventDefault();
    this.props.onAddUrl(this.state.url);
    this.setState({url: ''});
  }
});

module.exports = ImageForm;
