var React = require("react");

var Carousel = React.createClass({displayName: 'Carousel',
  propTypes: {
    images: React.PropTypes.array.isRequired,
    selected: React.PropTypes.number.isRequired,
    onClickLeft: React.PropTypes.func.isRequired,
    onClickRight: React.PropTypes.func.isRequired,
    onSelectImage: React.PropTypes.func.isRequired
  },

  render: function() {
    var left = this.props.selected * 300 * -1,
        ulStyle = {
          width: this.props.images.length * 300,
          "-ms-transform": "translate(" + left + "px,0px)",
          "-webkit-transform": "translate(" + left + "px,0px)",
          transform: "translate(" + left + "px,0px)"
        };

    return (
      React.createElement("div", null, 
        React.createElement("span", {className: "arrow left", 
              onClick: this.props.onClickLeft}, ""), 
        React.createElement("div", {className: "carousel-stage"}, 
          React.createElement("ul", {style: ulStyle, className: "carousel-list"}, 
            this.props.images.map(function(image, i) {
              return React.createElement("li", {key: i}, React.createElement("img", {src: image}));
            })
          ), 
          React.createElement("ul", {className: "dots"}, 
            this.props.images.map(function(image, i) {
              var activeClass = i === this.props.selected ? "active" : "";
              return React.createElement("li", {key: i, 
                         className: "circle " + activeClass, 
                         onClick: this.onClickDot.bind(this, i)});
            }.bind(this))
          )
        ), 
        React.createElement("span", {className: "arrow right", 
              onClick: this.props.onClickRight}, "")
      )
    )
  },

  onClickDot: function(index) {
    this.props.onSelectImage(index);
  }
});

module.exports = Carousel;
